import React, { useState } from 'react';
import { getGeminiResponse } from './getGeminiResponse';
import { marked } from 'marked';

const HikingChat = () => {
  const [question, setQuestion] = useState('');
  const [chatHistory, setChatHistory] = useState([]);
  const [loading, setLoading] = useState(false);

  const handleQuestionChange = (event) => {
    setQuestion(event.target.value);
  };

  const handleSendQuestion = async () => {
    if (question.trim() === '') return;

    const newHistory = [...chatHistory, { type: 'question', text: question }];
    setChatHistory(newHistory);
    setQuestion('');
    setLoading(true);

    const answer = await getGeminiResponse(question);

    // 응답에서 텍스트 부분만 추출
    let extractedText = '적절한 응답을 받지 못했습니다.';
    if (answer && answer.candidates && answer.candidates[0] && answer.candidates[0].content && answer.candidates[0].content.parts) {
      extractedText = answer.candidates[0].content.parts.map(part => part.text).join(' ');
    }

    setChatHistory([...newHistory, { type: 'answer', text: marked.parse(extractedText) }]);
    setLoading(false);
  };

  return (
    <div className='chat-container'>
      <div className='chat-window'>
        <div className='answer'>
          <strong>어떤 등산 정보를 원하시나요?</strong>
          <br />
          <small>검색 예) 40대 중년 등산객인데 월악산 등산로 추천해줘</small>
        </div>
        {chatHistory.map((entry, index) => (
          <div key={index} className={entry.type === 'question' ? 'question' : 'answer'}>
            {entry.type === 'answer' ? (
              <div dangerouslySetInnerHTML={{ __html: entry.text }} />
            ) : (
              entry.text
            )}
          </div>
        ))}
      </div>

      <div className='chat-input'>
        <textarea
          value={question}
          onChange={handleQuestionChange}
          placeholder="어떤 등산 정보를 원하시나요?"
        />

        <button onClick={handleSendQuestion}>
          {loading ? '...' : '질문'}
        </button>
      </div>
    </div>
  );
};

export default HikingChat;
