// TouristSpotList.js
import React from 'react';

const TouristSpotList = ({ selectedLocationName, tourismSpots }) => (
  <div className='tourList'>
    <div className='nearSpot'>"{selectedLocationName}" 주변 관광지</div>
    <ul>
      {tourismSpots.map((spot, index) => (
        <li key={index}>
          <strong>{spot.title} </strong>
          ({(parseFloat(spot.dist)/1000).toFixed(1)}km)<br />
          {spot.addr1}<br />
          
        </li>
      ))}
    </ul>
  </div>
);

export default TouristSpotList;
