import React, { useState, useEffect, useRef } from 'react';
import './App.css';

//컨포넌트
import HikingChat from './Components/HikingChat'; 
import Search from './Components/Search';
import TouristSpotList from './Components/TouristSpotList';
import UserLocationMarker from './Components/UserLocationMarker.js';

//이미지
import PeakMarker from './Image/peak_marker.png';
import DangerousMarker from './Image/dangerous_marker.png';
import ShelterMarker from './Image/shelter_marker.png';
import MeMarker from './Image/me_marker.png';
import ChevronLeft from './Image/chevron-left.svg';
import ChevronRight from './Image/chevron-right.svg';

//데이터
import HikingRiskAreas from './Data/hikingRiskAreas.json'; // 위험지역 데이터
import MountainPeaks from './Data/mountainPeaks.json'; // 산봉우리 데이터

import { Helmet } from 'react-helmet';


const kakaoApiKey = process.env.REACT_APP_KAKAO_API_KEY;
const tourismApiKey = process.env.REACT_APP_TOURISM_API_KEY;

/* global kakao */

function App() {
  
  const [map, setMap] = useState(null); // 지도 객체를 저장하는 상태 변수
    const mapContainer = useRef(null); // 지도 컨테이너를 참조하는 useRef 객체
    const mountainAreas = HikingRiskAreas // 산악 위험 지역 정보를 저장하는 상태 변수
    const mountainPeaks = MountainPeaks // 산악 봉우리 정보를 저장하는 상태 변수
    const [inputText, setInputText] = useState(''); // 입력 상태를 관리하는 상태 변수
    const [places, setPlaces] = useState([]); // 장소 검색 결과를 저장하는 상태 변수
    const [selectedLocation, setSelectedLocation] = useState(null); // 선택된 위치 정보를 저장하는 상태 변수
    const [selectedLocationName, setSelectedLocationName] = useState(null); // 선택된 위치의 이름을 저장하는 변수
    const [tourismSpots, setTourismSpots] = useState([]); // 주변 관광지 정보를 저장하는 상태 변수
    const [isSidebarOpen, setIsSidebarOpen] = useState(true); //사이드바 토클
    const [isSidebarOpen2, setIsSidebarOpen2] = useState(false); //사이드바 토클
    const toggleSidebar = () => {
        setIsSidebarOpen(!isSidebarOpen);
    };// 사이드바 토클
    const toggleSidebar2 = () => {
        setIsSidebarOpen2(!isSidebarOpen2);
    };// 사이드바 토클2


    // Kakao Maps API 스크립트 로드 후, 지도 초기화
    useEffect(() => {
        const script = document.createElement('script');
        script.async = true;
        script.src = `https://dapi.kakao.com/v2/maps/sdk.js?appkey=${kakaoApiKey}&autoload=false&libraries=services,clusterer`;
        document.head.appendChild(script);

        script.onload = () => {
            kakao.maps.load(() => {
                const container = mapContainer.current;
                const options = {
                    center: new kakao.maps.LatLng(36.88100097182494, 128.11089354647027), // 초기 지도 중심 좌표 설정
                    level: 5 // 초기 지도 확대/축소 레벨 설정
                };
                const newMap = new kakao.maps.Map(container, options); // 새로운 지도 객체 생성
                setMap(newMap); // 생성된 지도 객체를 상태 변수에 저장
            });
        };
    }, []);

    // 이펙트 함수는 지도 및 산악 정보(mountainAreas 및 mountainPeaks)가 변경될 때 호출됩니다.
    useEffect(() => {
        // 지도와 산악 지역 및 봉우리 데이터가 준비되어 있을 때만 실행합니다.
        if (map) {
            // 각 산봉우리에 대한 마커를 생성하고 설정합니다.
            mountainPeaks.forEach(peak => {
                const marker = new kakao.maps.Marker({
                    map: map,
                    position: new kakao.maps.LatLng(peak.lat, peak.lot),
                    title: peak.placeNm
                });
                // 봉우리 마커 이미지를 설정합니다.
                marker.setImage(new kakao.maps.MarkerImage(
                    PeakMarker, 
                    new kakao.maps.Size(50, 50)
                ));
            });

            // 각 산악 지역에 대한 마커를 생성하고 설정합니다.
            mountainAreas.forEach(area => {
                let imageSrc;
                // 산악 지역 유형에 따라 마커 이미지를 선택합니다.
                switch (area.plcTypeCd) {
                    case 'DANGER':
                        imageSrc = DangerousMarker;
                        break;
                    case 'SHELTER':
                        imageSrc = ShelterMarker;
                        break;
                    default:
                        imageSrc = DangerousMarker;
                }
                const marker = new kakao.maps.Marker({
                    map: map,
                    position: new kakao.maps.LatLng(area.lat, area.lot),
                    title: area.plcNm
                });
                // 산악 지역 마커 이미지를 설정합니다.
                marker.setImage(new kakao.maps.MarkerImage(
                    imageSrc, 
                    new kakao.maps.Size(50, 50)
                ));
            });
        }
    }, [map]);

     // 입력 텍스트 변경 시 호출되는 핸들러 함수
     const handleInputChange = (e) => {
        const { value } = e.target;
        setInputText(value);
        if (!value.trim()) {
            setPlaces([]); // 입력이 공백인 경우 장소 목록 초기화
            return;
        }
        searchPlaces(value); // 장소 검색 함수 호출
    };

    // Kakao Maps Places API를 사용하여 장소를 검색하는 함수
    const searchPlaces = (keyword) => {
        const ps = new kakao.maps.services.Places();
        const options = {
          size: 5 // 최대 검색 결과 수 설정
        };

        ps.keywordSearch(keyword, (data, status) => {
            if (status === kakao.maps.services.Status.OK) {
                setPlaces(data); // 검색 결과를 상태 변수에 설정
            } else {
                setPlaces([]); // 검색 결과가 없는 경우 빈 배열 설정
            }
        }, options);
    };

    // 장소 선택 시 호출되는 핸들러 함수
    const handlePlaceSelect = (place) => {
        //console.log(`Selected place: ${place.place_name}`);
        //console.log(`Address: ${place.address_name}`);
        //console.log(`Coordinates: Latitude ${place.y}, Longitude ${place.x}`);

        setSelectedLocationName(place.place_name);

        // 선택된 위치 좌표 설정
        setSelectedLocation({
            latitude: place.y,
            longitude: place.x
        });

        // 선택된 주소로 지도 이동
        if (map) {
            const moveLatLon = new kakao.maps.LatLng(place.y, place.x);
            map.panTo(moveLatLon);

            // 선택된 주소에 마커 추가
            const marker = new kakao.maps.Marker({
                map: map,
                position: moveLatLon,
                title: place.place_name
            });
            marker.setImage(new kakao.maps.MarkerImage(
                MeMarker, 
                new kakao.maps.Size(32, 32)
            ));
        }

        setPlaces([]);
    };

    // 선택된 위치 변경 시 호출되는 효과
    useEffect(() => {
        const fetchNearbyTourismSpots = async () => {
            if (selectedLocation) {
                const url = 'https://apis.data.go.kr/B551011/KorService1/locationBasedList1';
                const params = new URLSearchParams({
                    serviceKey: tourismApiKey,
                    numOfRows: '20',
                    pageNo: '1',
                    MobileOS: 'ETC',
                    MobileApp: 'SafeHikingTour',
                    arrange: 'A',
                    mapX: selectedLocation.longitude.toString(),
                    mapY: selectedLocation.latitude.toString(),
                    radius: '5000',
                    listYN: 'Y',
                    _type: 'json'
                });

                try {
                    const response = await fetch(`${url}?${params.toString()}`);
                    const data = await response.json();
                    setTourismSpots(data.response.body.items.item); // 주변 관광지 정보 설정
                } catch (error) {
                    console.error('Error fetching nearby tourism spots:', error);
                }
            }
        };

        fetchNearbyTourismSpots();
    }, [selectedLocation]);



  return ( 
    <div className="App">
      <header className="App-header">
        <Helmet>
            <title>안전산행투어</title>
            <meta name="description" content="등산 위험지역과 대피소 정보를 알려주는 지도" />
            <meta name="keywords" content="등산, 등산위험지역, 등산사고, 등산대피소, 등산위험" />
            <meta name="author" content="안전산행투어" />
            <meta property="og:title" content="안전산행투어" />
            <meta property="og:description" content="등산 위험지역과 대피소 정보를 알려주는 지도" />
            <meta property="og:url" content="https://moonjiuk.github.io/safehikingtour.github.io/" />
            <meta property="og:type" content="website" />
            <meta property="og:image" content="https://moonjiuk.github.io/safehikingtour.github.io/logo512.png" />
            <script type="application/ld+json">
            {`
            {
                "@context": "http://schema.org",
                "@type": "WebSite",
                "name": "안전산행투어",
                "url": "https://moonjiuk.github.io/safehikingtour.github.io/",
                "description": "등산 위험지역과 대피소 정보를 알려주는 지도",
                "keywords": "등산, 등산위험지역, 등산사고, 등산대피소, 등산위험"
            }
            `}
            </script>
        </Helmet>

        <div className='hikingMap'>
            <div className='mapContainer' ref={mapContainer}/>
            <div className='version'>v.0.2</div>
            
            
            <div className={`info ${isSidebarOpen ? 'open' : 'closed'}`}>
                <div className='infoToggle' onClick={toggleSidebar}>
                    <img src={isSidebarOpen ? ChevronLeft : ChevronRight} alt="Toggle Sidebar" />
                </div>
                <Search 
                    inputText={inputText} 
                    handleInputChange={handleInputChange} 
                    places={places} 
                    handlePlaceSelect={handlePlaceSelect} 
                />

                <TouristSpotList 
                    selectedLocationName={selectedLocationName} 
                    tourismSpots={tourismSpots} 
                />
            </div>

            <div className={`chatBot ${isSidebarOpen2 ? 'open' : 'closed'}`}>
                <div className='chatBotToggle' onClick={toggleSidebar2}>
                    <img src={isSidebarOpen2 ? ChevronRight : ChevronLeft} alt="Toggle Sidebar" />
                </div>
                
                <HikingChat />
            </div>

            {map && <UserLocationMarker map={map} MeMarker={MeMarker} />}
        </div>
      </header> 
    </div>
  ); 
}
 
export default App;

